import React from "react"
import ArticlePreface from "./ArticlePreface"
import ArticleSection from "./ArticleSection"
import BlankLink from "./BlankLink"
import ArticleImage from "./ArticleImage"

const Article1 = () => (
  <div>
    <ArticleImage src="/images/1.gif" />
    <ArticlePreface>
      You can click the folling link to interact with a Facebook Messenger implementation of <i>Stanley the Chatbot</i>:
      <br />
      <BlankLink href="https://www.messenger.com/t/letthetalenttalk/">
        https://www.messenger.com/t/letthetalenttalk/
      </BlankLink>
      <br />
      This implementation helps Talkpush (the company that creates <i>Stanley the Bot</i>) recruit talents. The chatbot have been updated since I left the project, but its core features still remain the same.
      <br /><br />
      You can also click the following link to visit its official page for some information:
      <br />
      <BlankLink href="https://talkpush.com/chatbot.html">
        https://talkpush.com/chatbot.html
      </BlankLink>

      <br /><br />
      Responsibilities:
      <ul>
        <li>Developing the chatbot service</li>
        <li>Developing relevant user interfaces for clients or system administrators to setup and customize the service with ease</li>
        <li>Participate in the design process</li>
      </ul>
      (I'm the major software engineer in this project. Other major team members include a product manager and a senior software engineer whom I consulted with)

      <br/><br />
      Major Skills:
      <br />
      Ruby, Rails, PostgreSQL, Facebook for Developers, WeChat Open Platform, Twilio, Amazon Web Services, Ubuntu (Linux), React, webpack, ES6 (JavaScript), CSS, HTML
    </ArticlePreface>
    <ArticleSection subhead="The Problem">
      <p>
        For organizations that <strong>interview high volumes of candidates</strong> such as call centers, hotels and business process outsourcing companies, <strong>arranging first-round interviews and conduct screening</strong> is a real pain. Scheduling and conducting interviews and managing information generated during interviews are typical processes that soak up a great deal of time and resources. Conventionally, phone interview is thought to be an comparatively desirable option, even with which a large portion of HR personnel is allocated to make hundreds of phone calls asking the same questions repeatedly while taking notes in a rush. What a drag!
      </p>
      <ArticleImage src="/images/1-1.jpg" />
    </ArticleSection>

    <ArticleSection subhead="The Solution">
      <p>
        <i>Talkpush</i>, a startup invested by <i>500 startups</i>, has been dedicated to addressing this problem with the power of <strong>information technology</strong>. With our solution, a client would spend a little time setting up campaigns in our system and specify what to ask for each campaign. Then they post their job openings mostly as they used to do on different channels, typically job board websites, but this time instructing applicants to send resumes to Talkpush. What happens next is that <strong>our software would condust interviews and taking notes (generating records into the system)</strong> on behalf of them. When the recruiters come back a few days later, usually there would be hundreds candidate records with extracted resume information and recorded interview answers for them to screen and they can manage candidates, e.g. shortlisting a candidate and send related e-mail to him, <strong>with our easy-to-use user interface</strong>.
      </p>
      <p>
        When the company was still based in Hong Kong, I joined the team and worked as the major developer of <i>Stanley the Bot</i>, before which our automated interview solution was based on phone calls. But you know what it is like to interact with an automated phone call: spend a long time listening to instructions, input numbers or talk and then start to listen again. It still left much to be desired.
      </p>
      <p>
        In June 2016, Facebook just lauched their <BlankLink href="https://developers.facebook.com/docs/messenger-platform"><strong>Messenger platform</strong></BlankLink>, so we decided to give it a try since instant messaging could <strong>potentially provide much better user experience</strong> and it's a <strong>much more preferred way of communication</strong> nowadays compared to phone calls. The platform inspired our management to consider developing our chatbot service, with which a job applicant could discover job openings, provide personal information, and conduct interviews via their favourite instant messaging apps such as Messenger or WeChat.
      </p>
      <p className="article-subhead">
        Development
      </p>
      <p>
        In the chatbot project, I took the responsibility of developing the chatbot and participated in the design process.
      </p>
      <p>
        My start point was Facebook's <BlankLink href="https://developers.facebook.com/docs/messenger-platform"><strong>Messenger Platform documentation</strong></BlankLink>. It documents <strong><BlankLink href="https://developers.facebook.com/docs/messenger-platform/webhook#format">what kind of data</BlankLink> you will receive when users ineract with Messenger</strong>, <strong><BlankLink href="https://developers.facebook.com/docs/messenger-platform/introduction/conversation-components">a set of conversation components</BlankLink> which you can utilize to present information</strong> and <strong>how you can instruct Messenger to display an instance of a component as you wish</strong>.
      </p>
      <p>
        The first thing I needed to figure out how to simply receive information about interaction from the Messenger side and react to it. I first created a Facebook App at <BlankLink href="https://developers.facebook.com">Facebook for Developers</BlankLink> and bound a specific <BlankLink href="https://www.facebook.com/business/products/pages">Facebook Page</BlankLink> to the App. Then I created an endpoint in our Rails application and provide the address of the enpoint in the Facebook App. To put it simple, now the Facebook Page and our application were connected through the Facebook App. So, whenever a <strong>user interacted</strong> with the specified Facebook Page, Facebook would deliver data to us describing the event, essentially telling us <strong>who</strong> interacted with the page and <strong>how</strong>. Then you could make a <strong>POST request</strong> with payload describing <strong>whom to reply to</strong> and <strong>what to reply</strong>, conforming to Facebook's <BlankLink href="https://developers.facebook.com/docs/messenger-platform/reference/send-api">Send API</BlankLink>. After Facebook receive it, the Messenger of that page would reply according to your instruction.
      </p>
      <p>
        Then it comes the more complicated part: to create the actual chatbot service to <strong>implement the business logic</strong>. So, I started to develope it with the excellent object-oriented programming language, <BlankLink href="https://www.ruby-lang.org/">Ruby</BlankLink>. The Chatbot is essentially a <strong>Ruby class</strong>. For each event which Facebook informed us of, we initialized an instance of the Ruby class to handle it.
      </p>
      <p>
        When you interact with an ATM machine, you know that there are different stages of the whole interaction. The ATM machine handles your inputs differently based on which stage your are in (e.g., it can treat your input of numbers as password or amount of money) and move to another stage based on your input. Behind this is usually what we called <strong>finite-state machine</strong>. As our chatbot works in a similar way, I also integrated it with state machine, where I specified what were the <strong>possible states</strong> and <strong>transitions for a certain state</strong>.
      </p>
      <p>
        As a developer, I was also actively involved in the design process as the team maintains a relatively flat structure. With this project, I really witnessed the difficult chanllenges posed in regard to user experience and the significance of good UX design. As <i>Stanley the Bot</i> is probably the first of its kind as a recruiting tool, different people have different problems interacting with it and we cannot foresee most of them. For example, although <i>Stanley</i> would clearly indicate that he's not an actual human being, many applicants still fail to interact with it according to instructions but talk to him freely as they would when talking to a real person. While we tried hard to bring AI capability to address this problem, we were also able to mitigate it by redesigning the experience. For example, the chatbot would abdicate by itself and notify the applicant that an actual person would take care of him later on when he has responded improperly during the same state for 3 times.
      </p>
    </ArticleSection>

  </div>
)

export default Article1



